import { Injectable } from '@angular/core';
import { ParseProvider } from '../parse/parse';
import Parse from 'parse';
import { LoadingController } from 'ionic-angular';

/*
  Generated class for the ServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ServiceProvider extends ParseProvider {

  public Loader;
  
  public how_page = false;
  public sharing_page = false;

  constructor(public loadingCtrl: LoadingController) {
    super();
    console.log('Hello ServiceProvider Provider');
  }

  getInformation() {
    return new Promise((resolve, reject) => {
      
      const Information = Parse.Object.extend("Information");
      var query = new Parse.Query(Information);

        query.get('ikUmf7mYhD').then((data) => {

          resolve(data.get('content'))
          
        }, (err) => {

          reject(err)

        });
    })
  }


  // fungsi mengambil data "what"
  retrieveWhatData() {
    return new Promise((resolve, reject) => {

      const What = Parse.Object.extend("Information")
      let q = new Parse.Query(What)

      q.equalTo('type', 1)
      q.equalTo('status', 1)

      q.first().then((response) => {  

        var data = this.JSONparse(response)

        resolve(data)

      }, (error) => {
        reject(error)
      })



    })
  }

  // fungsi mengambil data "how"
  retrieveHowData() {
    return new Promise((resolve, reject) => {

      const What = Parse.Object.extend("Information")
      let q = new Parse.Query(What)

      q.equalTo('type', 3)
      q.equalTo('status', 1)

      q.first().then((response) => {  

        var data = this.JSONparse(response)

        resolve(data)

      }, (error) => {
        reject(error)
      })



    })
  }

  JSONparse(object) {
    return object = JSON.parse(JSON.stringify(object));
  }

  LOADER(msg="Tunggu ya... USE sedang menyiapkan datanya :)") {
    this.Loader = this.loadingCtrl.create({ // function loading => contohnya dari sini https://ionicframework.com/docs/v3/components/#loading
      content: msg // content loading
    });
    this.Loader.present();
  }

  simpanDataResponden(data) {
    return new Promise((resolve, reject) => {
      console.log('simpanDataResponden')
      console.log('ini ada datanya', data)

      const Responden = Parse.Object.extend("Responden");
      var responden = new Responden();

      responden.set('nama', data.nama);
      responden.set('friends', data.friends);
      responden.set('disampaikan', data.disampaikan);
      responden.set('topik', data.topik);
      responden.set('media', data.media);
      responden.set('daftar', new Date().toLocaleDateString())

      responden.save().then((res) => {
        resolve(res);
      }, err => {
        reject(err)
      })

    })

  }


  simpanDataSharing(data) {
    return new Promise((resolve, reject) => {

      const Sharing = Parse.Object.extend("Sharing")
      let s = new Sharing ()

      s.save(data).then((res) => {
        var data = this.JSONparse(res)
        resolve(data)
      }, (err) => {
        reject(err)
      })


    })
  }



  ambilDataResponden() {
    return new Promise((resolve, reject) => {

      const Responden = Parse.Object.extend("Responden")
      let q = new Parse.Query(Responden);

      q.descending('createdAt');
      q.include('sharing');
      q.limit(50)
      q.doesNotExist('deletedAt')

      q.find().then((response) => {
        var data = this.JSONparse(response)

        resolve(data)
      }, err => {
        reject(err)
      })


    })
  }

  retrieveOperationalData() {
    return new Promise((resolve, reject) => {

      const What = Parse.Object.extend("Information")
      let q = new Parse.Query(What)

      q.equalTo('type', 4)
      q.equalTo('status', 1)

      q.first().then((response) => {  

        var data = this.JSONparse(response)

        resolve(data)

      }, (error) => {
        reject(error)
      })
    })
  }
  

  retrieveInformationData() {
    return new Promise((resolve, reject) => {

      const What = Parse.Object.extend("Information")
      let q = new Parse.Query(What)

      q.doesNotExist('deletedAt');

      q.find().then((response) => {  

        var data = this.JSONparse(response)

        resolve(data)

      }, (error) => {
        reject(error)
      })
    })
  }


  saveData(className, data) {
    return new Promise((resolve, reject) => {

      const Data = Parse.Object.extend(className)
      let q = new Parse.Query(Data)      

      q.get(data.objectId).then((response) => {

        // response.set('deletedAt', new Date())

        response.save(data).then((ok) => {
          var d = this.JSONparse(ok)
  
          resolve(d)

        }, err => {
          var d = this.JSONparse(response)
  
          resolve(d)
        })
      }, (error) => {
        reject(error)
      })
    })
  }
}



