import { Injectable } from '@angular/core';
import Parse from 'parse'

/*
  Generated class for the ParseProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ParseProvider {
  
  private app_id = "eLsQxcVVxTutfKQO6cilnKwGQOPzINnGiNXOIG1y";
  private js_key = "Y3gxfxSCwYXtMnPUsGpfaZYZE5lPYl9nfxRoRhmX"

  constructor() {
    console.log('Hello ParseProvider Provider');
    this.parseInitialize()
  }

  private parseInitialize() {
    Parse.initialize(this.app_id, this.js_key);
    Parse.serverURL = "https://parseapi.back4app.com/"
  }

  namaFungsi() {
    console.log('contoh')
  }

}



